                                                      </el-table-column>
                                                </el-table>
                                          </el-tab-pane>
                                    </el-tabs>
                                    <!--<div class="box1">
                                          <span class="tab"></span>
                                          <div class="btns">
                                                <el-button size="small" type="primary"></el-button> 
                                                <el-button size="small"></el-button>
                                                <el-button size="small"></el-button>
