                                                <el-button size="small"></el-button>  
                                          </div>
                                    </div>
                                    <div class="box2">
                                          <div class="left">
                                                <el-radio-group v-model="radio">
                                                      <el-radio :label="1"></el-radio>
                                                      <el-radio :label="2"></el-radio>
                                                      <el-radio :label="3"></el-radio>
                                                      <el-radio :label="4">/</el-radio>
                                                </el-radio-group>
                                          </div>
                                          <div class="right">
                                                <div class="in"><el-input size="small" placeholder="" class="key"></el-input></div>
                                                <div class="in">
                                                      <el-button size="small" type="success"></el-button>
                                                      <el-button size="small" type="warning"></el-button>
                                                </div>
                                          </div>
                                    </div>
                                    <div class="box3">
                                          <span class="tab"></span>
                                          <span class="co_green"></span>
                                          <span class="tab" style="margin-left:20px;"></span>
                                          <span class="co_green"></span>
                                    </div>-->
                                    <div class="box4">
                                          <div class="date_bar">
                                                <!--<span class="tab"></span>
                                                <div style="display:inline-block;"><el-date-picker size="small" v-model="value1" type="datetime" placeholder=""></el-date-picker></div>
                                                <span class="tab" style="margin-left:20px;"></span>
