                                                <el-checkbox v-model="checked"></el-checkbox>
                                          </div>-->
                                    </div>
                                    <!--<div class="box5">
                                          <el-table :data="tableData" style="width: 100%">
                                                <el-table-column prop="type" label=""></el-table-column>
                                                <el-table-column prop="name" label="/">
                                                      <template slot-scope="scope">
                                                            <el-input size="mini"></el-input>/234
                                                            <i class="el-icon-edit"></i>
                                                      </template>
                                                </el-table-column>
                                                <el-table-column prop="canOrder" label=""></el-table-column>
                                                <el-table-column prop="canExceed" label=""></el-table-column>
                                                <el-table-column prop="reserve" label="" width="160">
                                                      <template slot-scope="scope">
                                                            <el-input-number size="mini"></el-input-number>
                                                      </template>
                                                </el-table-column>
                                                <el-table-column prop="notAllot" label="" width="180"></el-table-column>
                                                <el-table-column prop="address" label="" width="180">
                                                      <template slot-scope="scope">
                                                            <el-button size="mini" type="warning"></el-button>
                                                      </template>
                                                </el-table-column>
                                          </el-table>
                                    </div>-->
                                    <div class="box6">
                                          <el-tabs type="border-card">
                                                <el-tab-pane label="">
                                                      <el-input size="small" v-model="guest" placeholder=""></el-input><b class="co_red">*</b>
                                                      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                                                      <el-input size="small" v-model="phone" placeholder=""></el-input></el-input><b class="co_red">*</b>
                                                      <div class="inner">
                                                            <el-input type="textarea" v-model="remark"></el-input>
                                                      </div>
                                                      <el-button class="btn" size="small" type="success" @click="reserve"></el-button>
                                                </el-tab-pane>
                                          </el-tabs>
                                    </div>      
                              </div>
                        </div>
                  </div>
            </div>
      </div>
</template>
<script>
      import axios from 'axios'
      import config from '../../config'
      import headerNav from '../header.vue'
      export default {
            data(){
                  return {
                        userInfo: '',
                        tableData: [{
                              type: '',
                              canOrder: 3,
                              reserve: 0,
                              canExceed: 4,  
                              notAllot: "",
                        }],
                        roomId: this.$route.query.roomId,
                        dateRange: [],
                        needDays: null,
                        guest: '',
                        phone: '',
                        remark: ''
                  }
            },
            components: {headerNav},
            computed: {
                  nowTime(){
                        let time = new Date();
                        let y = time.getFullYear();
                        let m = time.getMonth() + 1;
				let d = time.getDate();
                        return y+"-"+m+"-"+d;
                  }
            },
            methods:{
                  async getList(){
                        let list = await http.get({
                              url: 'reserve',
                              data: {
                                    room_id: this.roomId,
                                    is_checked: 0,
                                    per_page: 300
                              }
                        })
                        if(list.code===4000&&list.data&&list.data.data){
                              this.tableData = list.data.data;
                        }
                  },
                  changeDate(){
                        let a = new Date(this.dateRange[0]);
                        let b = new Date(this.dateRange[1]);
                        this.needDays = (b-a)/1000/3600/24;
                  },
                  async reserve(){
                        var that = this;
                        if(!this.dateRange[0]){
					Msg.prompt('');
					return;
				}
                        if(!this.guest){
					Msg.prompt('');
					return;
				}
                        if(!this.phone){
					Msg.prompt('');
					return;
				}
                        var phoneReg=/^[1][3,4,5,7,8][0-9]{9}$/;
				if(!phoneReg.test(that.phone)){
					Msg.prompt('');
				}
                        let requestData = {
                              room_id: this.roomId,
                              start: this.dateRange[0],
                              end: this.dateRange[1],
                              day: this.needDays,
                              name: this.guest,
                              phone: this.phone,
                              status: 1
                        }
                        if(this.remark){
                              requestData.remark = this.remark;
                        }
                        let rese = await http.post({
                              url: 'reserve',
                              data: requestData
                        })
                        if(rese.code===4000){
                              Msg.prompt("");
                              this.$router.push({path:'/index'});
                        }
                  },
                  async removeOrder(row){
                        let del = await http.delete({
                              url: 'reserve/'+row.id,
                              data: {}
                        })
                        if(del.code===4000){
                              Msg.prompt('');
                              this.getList();
                        }
                  }
            },
            created(){
                  this.getList();
                  this.userInfo = JSON.parse(sessionStorage.getItem('userInfo')).record;
            },
            mounted(){
                  
            }
      }


</script>






